

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1 {
  font-size: 1.4rem;
  color: #ecc238;
}

.App {
  text-align: center;
  background-color: #203337;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 62.5%;
  color: white;
}
.input {
    background-color: #e1e1e1;
    text-align: center;
    font-size: 1.15rem;
    color: #333;
    padding: 1rem;
    box-sizing: border-box;
    border-radius: 0.4rem;
    border: 0.3rem solid #e1e1e1;
    width: 95%;
}

input:focus {
    border: 0.3rem solid #e1e1e1;
    outline: none;
}

.label {
    display: none;
}

@media screen and (min-width: 321px) {
	.input {
		width: 90%;
	}
}

@media screen and (min-width: 481px) {
	.input {
		width: 90%;
	}

}
@media screen and (min-width: 601px) {
	.input {
		width: 70%;
	}

}
@media screen and (min-width: 801px) {
	.input {
		width: 70%;
	}
}

@media screen and (min-width: 1025px) {
	.input {
		width: 30%;
	}
}

@media screen and (min-width: 1921px) {
	.input {
		width: 20%;
	}
}
.button {
	background-color:#cda00e;
	border-radius: 0.4rem;
	border: 0.3rem solid #cda00e;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
    width: 95%;
    margin-top: 1rem;
	font-family:Arial;
	font-size: 1.15rem;
	padding: 1rem;
	text-decoration:none;
}
.button:hover {
	background-color:#ecc238;
    border: 0.3rem solid #ecc238;
}
.button:active {
	position:relative;
	top: 0.1rem;
}

@media screen and (min-width: 321px) {
	.button {
		width: 90%;
	}
}

@media screen and (min-width: 481px) {
	.button {
		width: 90%;
	}

}
@media screen and (min-width: 601px) {
	.button {
		width: 70%;
	}

}
@media screen and (min-width: 801px) {
	.button {
		width: 70%;
	}
}

@media screen and (min-width: 1025px) {
	.button {
		width: 30%;
	}
}

@media screen and (min-width: 1921px) {
	.button {
		width: 20%;
	}
}
.form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 1.8rem;
    position: relative;
}
.datagrid {
    background-color: #e1e1e1;
    position: fixed;
    padding: 2rem 1rem 2rem 1rem;
    width: 90%;
    height: 400px;
    display: flex;
    margin-top: 1.1rem;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
		font-weight: bold;
    font-size: 1.2rem;
    text-align: left;
    color: #444;
    border-radius: 0.4rem;
		border: 3px solid #999;
}

.close-button {
	position: absolute;
	top: -25px;
	right: -25px;
	background: transparent;
	border: none;
	cursor: pointer;
}

.datagrid ul {
    list-style-type: none;
    margin-left: -3rem;
    display: block;
}

@media screen and (min-width: 321px) {
	.datagrid {
		width: 90%;
	}
}

@media screen and (min-width: 481px) {
	.datagrid {
		width: 90%;
	}

}
@media screen and (min-width: 601px) {
	.datagrid {
		width: 70%;
	}

}
@media screen and (min-width: 801px) {
	.datagrid {
		width: 70%;
	}
}

@media screen and (min-width: 1025px) {
	.datagrid {
		width: 30%;
	}
}

@media screen and (min-width: 1921px) {
	.datagrid {
		width: 20%;
	}
}
.error {
    margin-top: 29rem;
    border-radius: 10px;
    font-size: 1rem ;
    color: #e1e1e1;
    position: fixed;
}
